export default {
	data() {
		return {
			reason:'请选择',
			show:false,
			value:'',
			phone:'',
			form: {
				collect: [{
					time: '2019-04-06 11:37',
					shopName: '麦田圈官网旗舰店',
					state: 5,

					goodsList: [{
						title: '香辣牛肉干',
						price: 88.88,
						image: '/static/goods.png',
						number: 1,
						attr: '规格 10*200g'
					}]
				}],
				goodsObj: {
					remarks: '香辣味的',
					allPay: '120',
					jifen: '100',
					preferential: '20.00',
					total: '880'
				},
			},
			statuss:'',
			show:false,
			marginBottom:'',
			fileList: [],
			columns: ['七天无理由退换', '商品损坏', '商品过期', '物流', '其他']
		}
	},
	onLoad(option){
		
	},
	methods: {
		consult(){
			uni.navigateTo({
				url:'consult'
			})
		},
		afterRead(event) {
		      const { file } = event.detail;
		      // 当设置 mutiple 为 true 是 file 是一个数组，mutiple 默认为 false，file 是一个对象
		      wx.uploadFile({
		        url: 'https://example.weixin.qq.com/upload', //仅为示例，非真实的接口地址
		        filePath: file.path,
		        name: 'file',
		        formData: { 'user': 'test' },
		        success (res){
		          // 上传完成需要更新fileList
		          const { fileList = [] } = this.data;
		          fileList.push({ ...file, url: res.data });
		          this.setData({ fileList });
		        }
		      });
		    },
			
		onCancel(){
			this.show = false
		},
		cellClick(){
			this.show = true
		},
		onConfirm(event){
			this.show = false;
			const { picker, value, index } = event.detail;
			this.reason = value;
			// console.log(`当前值：${value}, 当前索引：${index}`)
		}
	}
}
